/*global define: false cyrillicToLatin: true*/

(function(global, factory) {
  // CommonJS
  if (typeof exports === 'object' && exports) {
    module.exports = factory()
  }
  // AMD
  else if (typeof define === 'function' && define.amd) {
    define(factory)
  }
  // global
  else {
    global.cyrillicToLatin = factory()
  }
}(this, function () {

  'use strict';

  return function(string) {
    var cyrillic = '_____________________________________________________________________________'.split('_')
    var latin = 'A_B_V_G_D__E___Z_I_J_J_K_L_Lj_M_N_Nj_O_P_R_S_T__U_F_H_C__D____Y_____a_b_v_g_d__e___z_i_j_j_k_l_lj_m_n_nj_o_p_r_s_t__u_f_h_c__d____y____'.split('_')

    return string.split('').map(function(char) {
      var index = cyrillic.indexOf(char)
      if (!~index)
        return char
      return latin[index]
    }).join('')
  }

}))
